import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  loadAllProject,
  loadQRCode,
} from '../../reducers'
import AppContainer from '../../components/AppContainer';
import QRMark from '../../components/QRMark';
import QRView from '../../components/QRView';

function basename(path) {
    return path.replace(/\\/g,'/').replace( /.*\//, '' );
}

function QRMarkContainer({ children }) {
  return (
    <div style={{
      display: 'inline-block',
      margin: 10,
    }}>
      { children }
    </div>
  )
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  projectId() {
    return basename(this.props.pathname);
  }

  componentDidMount() {
    this.props.loadAllProject((projects) => {
      this.props.loadQRCode('/recept', 'receptQRCode');
      this.props.loadQRCode('/qr-code-system/reset', 'resetQRCode');
      this.setState({
        projects,
      });
    });
  }

  render() {
    console.log(this.state.projects);
    const views = ((projects, col) => {
      if (!projects) return [];
      const r = [];
      let t = [];
      projects.forEach( (v, i) => {
        t.push(v);
        if ((i % col) === col-1) {
          r.push(t);
          t = [];
        }
      });
      if (t.length > 0) {
        r.push(t);
      }
      return r;
    })(this.state.projects, 2);
    return (
      <div>
        {
          views.map( (v, i) => {
            return (
              <div key={i}>
                {
                  v.map( (p, i) => {
                    return (
                      <QRMarkContainer key={i}>
                        <QRMark
                          index={p.index}
                          width={315}
                          title={p.title}
                          qrcode={p.qrCode}
                        >
                          <div style={{ textAlign: "center", marginTop: 20, marginBottom: 20, }}>
                            <span style={{ fontWeight: 'bold', fontSize: 20, marginRight: 10, }}></span><a href="#" className="arrow sample3-2"></a>
                          </div>
                        </QRMark>
                      </QRMarkContainer>
                    )
                  })
                }
              </div>
            )
          })
        }
        <div>
          <QRMarkContainer>
            <QRView
              index={0}
              width={315}
              title={["QR"]}
              qrcode={this.props.receptQRCode}
            />
          </QRMarkContainer>
          <QRMarkContainer>
            <QRView
              index={0}
              width={315}
              title={["QR"]}
              qrcode={this.props.resetQRCode}
            />
          </QRMarkContainer>
        </div>
      </div>
    );
  }
}

const props = state => ({
  pathname: state.router.location.pathname,
  search: state.router.location.search,
  hash: state.router.location.hash,
  width: state.app.width,
  height: state.app.height,
  receptQRCode: state.app.receptQRCode,
  resetQRCode: state.app.resetQRCode,
})

const actions = dispatch => ({
  loadAllProject: (callback) => dispatch( loadAllProject(callback) ),
  loadQRCode: (url, key, calback) => dispatch( loadQRCode(url, key, calback) ),
})

export default connect(props, actions)(App);



