import React, { Component } from 'react';
import '../pages/App.css';
import { connect } from 'react-redux'

class QRView extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    const fontSize = ((w) => { let t = w / 35; if (t < 18) t = 18; return t; })(this.props.width);
    const marginScale = 1*300/this.props.width;
    const { title } = this.props;
    console.log(`title ${title}`);
    return (
      <div className="QRMarkContainer" style={{ width: this.props.width, textAlign: 'center', }}>
        <h2 className="QRMarkHeader" style={{ margin: 0, fontSize: fontSize*0.7, position: 'relative', top: 0, }}> QR </h2>
        <div className="QRMarkBorder" style={{ marginTop: 30*marginScale, marginBottom: 30*marginScale, paddingTop: 10*marginScale, paddingBottom: 30*marginScale }}>
          {
            (title) ? (
              <div className="QRMarkTitle" style={{ width: this.props.width-10 }}>
                {
                  (title.length <= 1) ? (
                    title.map( (v, i) => {
                      return <h1 key={i} style={{ margin: 0, marginBottom: 13*marginScale, marginTop: 10*marginScale, fontSize: fontSize*1.5, }}> {v} </h1>
                    })
                  ) : (
                    title.map( (v, i) => {
                      return <h1 key={i} style={{ margin: 0, marginBottom: 5*marginScale, fontSize, }}> {v} </h1>
                    })
                  )
                }
              </div>
            ) : null
          }
          <img style={{ border: 'solid 1px', imageRendering: 'pixelated', }}  width={this.props.width/2} height={this.props.width/2} src={this.props.qrcode} />
        </div>
        {
          this.props.children
        }
      </div>
    )
  }
}

QRView.defaultProps = {
  index: 1,
  width: 300,
  title: [''],
  qrcode: 'data:image/png;base64,',
}

const props = state => { return ({}) }
const actions = dispatch => ({})

export default connect(props, actions)(QRView);



