import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  vote,
  push,
} from '../../reducers'
import AppContainer from '../../components/AppContainer';
import QRHeader from '../../components/QRHeader';
import config from '../../config';

function Container({ children, width, }) {
  return (
    <div>
      <QRHeader width={width} > QR </QRHeader>
      <AppContainer>
        <div style={{ width: width-40-6, border: 'solid 3px #0080FF', margin: 20, paddingTop: 50, paddingBottom: 50, borderRadius: 15 }}>
          { children }
        </div>
      </AppContainer>
    </div>
  )
}

function basename(path) {
    return path.replace(/\\/g,'/').replace( /.*\//, '' );
}

class App extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {}
  }

  projectId() {
    return basename(this.props.pathname);
  }

  componentDidMount() {
    if (this.props.voted[this.projectId()]) {
    } else {
      this.props.vote(this.projectId());
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.user !== nextProps.user) {
      if (nextProps.user.state === 'end') {
        this.props.history.push('/end')
      }
    }
  }

  render() {
    const fontSize = ((w) => { let t = w / 21; if (t < 18) t = 18; return t; })(this.props.width);
    console.log(this.props.user);
    if (this.props.state === 'not found') {
      return (
        <Container width={this.props.width}>
          <h1 style={{ color: '#0080FF', fontSize, }}> { config.title } </h1>
          <div style={{ fontSize }}>
            
          </div>
        </Container>
      )
    }
    if (!this.props.user) {
      return <Container width={this.props.width}><div style={{ fontSize }}> ... </div></Container>
    }
    if (this.props.user.state !== 'logined') {
      return <Container width={this.props.width}>
        <h1 style={{ color: '#0080FF', fontSize, }}> { config.title } </h1>
        <div style={{ fontSize }}>  </div>
      </Container>
    }
    const title = (() => {
      return (this.props.voted[this.projectId()]) ? this.props.voted[this.projectId()].title : null;
    })();
    const marginScale = 1*300/this.props.width;
    return <div className="App">
      {
        (title) ? 
        <Container width={this.props.width}>
          <h1 style={{ color: '#0080FF', fontSize, }}> { config.title } </h1>
          <div>
            {
              (title) ? (
                <div className="QRMarkTitle">
                  {
                    (title.length <= 1) ? (
                      title.map( (v, i) => {
                        return <h1 key={i} style={{ margin: 0, marginBottom: 13*marginScale, marginTop: 10*marginScale, fontSize: fontSize*1.5, }}> {v} </h1>
                      })
                    ) : (
                      title.map( (v, i) => {
                        return <h1 key={i} style={{ margin: 0, marginBottom: 5*marginScale, fontSize, }}> {v} </h1>
                      })
                    )
                  }
                </div>
              ) : null
            }
            <p style={{ fontSize }}>
              
            </p>
            {
              (3-this.props.user.vote > 0) ?
              <p style={{ fontSize, marginTop: 20, }}>
                
              </p> : null
            }
            {
              (3-this.props.user.vote <= 0) ?
              <p style={{ fontSize, marginTop: 20, }}>
                
              </p> :
              <p style={{ fontSize, marginTop: 20, }}>
                { 3-this.props.user.vote }
              </p>
            }
          </div>
        </Container> :
        <Container width={this.props.width}><div style={{ fontSize }}>
          ...
        </div></Container>
      }
    </div>
  }
}

const props = state => {
  return ({
    pathname: state.router.location.pathname,
    search: state.router.location.search,
    hash: state.router.location.hash,
    voted: state.app.voted,
    state: state.app.state,
    user: state.app.user,
    width: state.app.width,
  })
}

const actions = dispatch => ({
  vote: (projectId, callback) => dispatch( vote(projectId, callback) ),
  push: (url) => dispatch( push(url) ),
})

export default connect(props, actions)(App);



