import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  load,
  push,
} from '../../reducers'

function basename(path) {
    return path.replace(/\\/g,'/').replace( /.*\//, '' );
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      project: {
        id: '',
        title: '',
      },
    }
  }

  projectId() {
    return basename(this.props.pathname);
  }

  componentDidMount() {
    if (this.props.new) {
    } else {
      this.props.load(this.projectId());
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.project !== nextProps.project) {
      this.setState({
        project: nextProps.project,
      })
    }
  }

  handleChangeId = (e) => {
    this.setState({ project: { ...this.state.project, id: e.target.value }});
  }

  handleChangeTitle = (e) => {
    this.setState({ project: { ...this.state.project, title: e.target.value }});
  }

  submitCancel = (e) => {
    this.props.history.push('/list');
  }

  submitEdit = (e) => {
    this.props.history.push('/list');
  }

  submitDelete = (e) => {
    this.props.history.push('/list');
  }

  submitNew = (e) => {
    this.props.history.push('/list');
  }

  submitQRCode = (e) => {
    this.props.history.push(`/qr/${this.projectId()}`);
  }

  render() {
    return (!this.props.new) ? 
    (
      <div className="App">
        <p>
          <span> ID: </span>
          <input className="addPageTextInput" type="text" value={this.projectId()} readOnly />
        </p>
        <p>
          <span> : </span>
          <input className="addPageTextInput" type="text" value={this.state.project.title} onChange={this.handleChangeTitle} />
        </p>
        <input type="button" value="" onClick={this.submitCancel} />
        <input type="button" value="" onClick={this.submitEdit} />
        <input type="button" value="" onClick={this.submitDelete} />
        <input type="button" value="QR" onClick={this.submitQRCode} />
      </div>
    ) : 
    (
      <div className="App">
        <p>
          <span> ID: </span>
          <input className="addPageTextInput" type="text" value={this.state.projectId} onChange={this.handleChangeId} />
        </p>
        <p>
          <span> : </span>
          <input className="addPageTextInput" type="text" value={this.state.project.title} onChange={this.handleChangeTitle} />
        </p>
        <input type="button" value="" onClick={this.submitCancel} />
        <input type="button" value="" onClick={this.submitNew} />
        <input type="button" value="QR" onClick={this.submitQRCode} />
      </div>
    )
  }
}

const props = state => ({
  pathname: state.router.location.pathname,
  search: state.router.location.search,
  hash: state.router.location.hash,
  project: state.app.project,
})

const actions = dispatch => ({
  load: (projectId, callback) => dispatch( load(projectId, callback) ),
  push: (url) => dispatch( push(url) ),
})

export default connect(props, actions)(App);



