import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  makeQRCode,
} from '../../reducers'
import AppContainer from '../../components/AppContainer';
import QRMark from '../../components/QRMark';

function basename(path) {
    return path.replace(/\\/g,'/').replace( /.*\//, '' );
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  projectId() {
    return basename(this.props.pathname);
  }

  componentDidMount() {
    this.props.makeQRCode(this.projectId(), () => {
      this.forceUpdate()
    });
  }

  render() {
    console.log(this.props.project);
    return (
      <AppContainer>
        <div style={{ padding: 20, }} >
          <QRMark
            index={this.props.project.index}
            width={this.props.width-2-40}
            title={this.props.project.title}
            qrcode={this.props.qrcode}
          />
        </div>
      </AppContainer>
    );
  }
}

App.defaultProps = {
  project: {
    title: [],
  },
}

const props = state => ({
  pathname: state.router.location.pathname,
  search: state.router.location.search,
  hash: state.router.location.hash,
  width: state.app.width,
  height: state.app.height,
  project: state.app.project,
  qrcode: state.app.qrcode,
})

const actions = dispatch => ({
  makeQRCode: (projectId, callback) => dispatch( makeQRCode(projectId, callback) ),
})

export default connect(props, actions)(App);



