import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import thunk from 'redux-thunk'

import AddPage from './pages/AddPage';
import ListPage from './pages/ListPage';
import QRPage from './pages/QRPage';
import RankingPage from './pages/RankingPage';
import VotePage from './pages/VotePage';
import ReceptPage from './pages/ReceptPage';
import EndPage from './pages/EndPage';
import QRPrintPage from './pages/QRPrintPage';

import registerServiceWorker from './registerServiceWorker';

import { createBrowserHistory } from 'history'
import { applyMiddleware, compose, createStore } from 'redux'
import { connectRouter, routerMiddleware } from 'connected-react-router'

import { Provider } from 'react-redux'
import { Route, Switch } from 'react-router' // react-router v4
import { ConnectedRouter } from 'connected-react-router'
import rootReducer from './reducers';
import {
  socket,
  loadRanking,
  loadStatus,
} from './reducers';

const history = createBrowserHistory()

const initialState = {
  app: {
    voted: {
      hello: { title: 'Hello World' },
    },
    project: {},
    projects: [
    ],
    statistics: [
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
      { title: [''], point: 0 },
    ],
    width: window.innerWidth,
    height: window.innerHeight,
  },
}

const store = createStore(
  connectRouter(history)(rootReducer),
  initialState,
  compose(
    applyMiddleware(
      routerMiddleware(history),
      thunk,
    ),
  ),
)

socket.on('connect', () => {
  console.log('connect');
  store.dispatch(loadRanking());
});

socket.on('updated', () => {
  console.log('updated');
  store.dispatch(loadRanking());
});

store.dispatch(loadStatus());

ReactDOM.render(
<Provider store={store}>
  <ConnectedRouter history={history}>
    <div style={{ height: '100%' }}>
      <Switch>
        <Route exact path="/qr-code-system/list" component={ListPage} />
        <Route exact path="/qr-code-system/edit/*" component={AddPage} />
        <Route exact path="/qr-code-system/new" render={() => (<AddPage new />)} />
        <Route exact path="/qr-code-system/qr/*" component={QRPage} />
        <Route exact path="/qr-code-system/qr-print" component={QRPrintPage} />
        <Route exact path="/qr-code-system/reset" render={() => (<ReceptPage reset />)} />
        <Route exact path="/ranking" component={RankingPage} />
        <Route exact path="/recept" component={ReceptPage} />
        <Route exact path="/end" component={EndPage} />
        <Route component={VotePage} />
      </Switch>
    </div>
  </ConnectedRouter>
</Provider>,
document.getElementById('react-root'));
registerServiceWorker();



