import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  changeWindowSize,
  loadRanking,
} from '../../reducers'
import RankingBar from './RankingBar';
import Image from './Image';
import config from '../../config';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      height: window.innerHeight,
      instruction: false,
    }
    this.intervalTimer = null;
  }

  onResize = () => {
    this.props.changeWindowSize({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this.onResize, false);
    this.props.loadRanking();
    // this.intervalTimer = setInterval(() => {
    //   this.setState({
    //     instruction: (!this.state.instruction),
    //   });
    // }, 10*1000);
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
    if (this.intervalTimer) clearInterval(this.intervalTimer);
    this.intervalTimer = null;
  }

  render1() {
    const { width, height, statistics } = this.props;
    const count = statistics.length;
    const barHeight = ((v) => { const a = height / (count+1); return a > v ? v: a; })(100)-7;
    const barSize = width-20;
    const maxPoint = (() => { let v = 0; statistics.forEach ( t => { if (t.point > v) v = t.point; } ); return v; })();
    const barScale = (barSize-width/10)/maxPoint;
    return (
      <div className="App">
        <p style={{ fontSize: barHeight,fontWeight: 'bold', color: '#0080FF' }} >
          { config.title }{ config.statistics_title }
        </p>
        {
          statistics.map( (v, i) => {
            v.index = i+1;
            return v;
          }).sort( (a,b) => {
            if (a.point > b.point) return -1;
            if (a.point < b.point) return  1;
            if (a.index < b.index) return -1;
            if (a.index > b.index) return  1;
            return 0;
          }).map( (v, i) => {
            return <RankingBar
              key={i}
              title={`${v.title.join('')}`}
              index={v.index}
              point={v.point}
              barScale={barScale}
              width={barSize}
              height={barHeight}
            />
          })
        }
      </div>
    );
  }

  render2() {
    const { width, height } = this.props;
    return (
      <div style={{ width: '100%', margin: 0, padding: 0, }} >
        <Image src="/images/instruction.jpeg" width={width-8} height={height-8} style={{ margin: 'auto', }}/>
      </div>
    )
  }

  render() {
    return (!this.state.instruction) ? this.render1() : this.render2();
  }
}

const props = state => ({
  pathname: state.router.location.pathname,
  search: state.router.location.search,
  hash: state.router.location.hash,
  width: state.app.width,
  height: state.app.height,
  statistics: state.app.statistics,
})

const actions = dispatch => ({
  changeWindowSize: (size) => dispatch( changeWindowSize(size) ),
  loadRanking: () => dispatch( loadRanking() ),
})

export default connect(props, actions)(App);



