import React, { Component } from 'react';
import config from '../../config';

class RankingBar extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const nanCheck = (v) => {
      if (isNaN(v)) return 0;
      return v;
    }
    const { width, height, fontScale, point, barScale, } = this.props;
    const fontSize = parseInt(height * fontScale);
    return <div className="RankingBarContainer" style={{ height }}>
      <div style={{ position: 'absolute', left: 30, zIndex: 2, fontWeight: 'bold', }}>
        <p style={{ lineHeight: `${height}px`, fontSize, }}> { (this.props.title!=='')?` ${('00'+this.props.index).slice(-2)}`:'' } </p>
      </div>
      <div style={{ position: 'absolute', left: fontSize*6, zIndex: 2, fontWeight: 'bold',}}>
        <p style={{ lineHeight: `${height}px`, fontSize, }}> <span style={{ color: '#FF644E', }}>{ this.props.title }</span> <span style={{ color: 'blue', }}> {(this.props.title!=='')?this.props.point:''}{(this.props.title!=='')?config.time_string:''} </span> </p>
      </div>
      <svg xmlns="http://www.w3.org/2000/svg" width={nanCheck(width)} height={height} style={{ position: 'relative', display: 'inline',  zIndex: 1, }}>
        <rect
          x={0}
          y={0}
          width={nanCheck(width)}
          height={height}
          style={{ fill: '#EEEEEE' }}
        />
        <rect
          x={0}
          y={0}
          width={nanCheck(point*barScale)}
          height={height}
          style={{ fill: '#CBE6F7' }}
        />
      </svg>
    </div>
  }
}

RankingBar.defaultProps = {
  width: 800,
  height: 60,
  fontScale: 0.75,
  point: 0,
}

export default RankingBar;



