import React, { Component } from 'react';
import '../App.css';
import { connect } from 'react-redux'
import {
  reset,
  recept,
} from '../../reducers'
import AppContainer from '../../components/AppContainer';
import QRHeader from '../../components/QRHeader';
import config from '../../config';

function basename(path) {
    return path.replace(/\\/g,'/').replace( /.*\//, '' );
}

function Container({ children, width, }) {
  return (
    <div>
      <QRHeader width={width} > QR </QRHeader>
      <AppContainer>
        <div style={{ width: width-40-6, border: 'solid 3px #0080FF', margin: 20, paddingTop: 50, paddingBottom: 50, borderRadius: 15 }}>
          { children }
        </div>
      </AppContainer>
    </div>
  )
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  projectId() {
    return basename(this.props.pathname);
  }

  componentDidMount() {
    if (this.props.reset) {
      this.props.resetVote();
    } else {
      this.props.recept();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (!this.props.reset) {
      if (this.props.user !== nextProps.user) {
        if (nextProps.user.state === 'end') {
          this.props.history.push('/end')
        }
      }
    }
  }

  render() {
    const fontSize = ((w) => { let t = w / 21; if (t < 18) t = 18; return t; })(this.props.width);
    if (!this.props.user) {
      return <Container width={this.props.width}>
        <h1 style={{ fontSize, }}> ... </h1>
      </Container>
    }
    if (this.props.user.state !== 'logined') {
      return <Container width={this.props.width}>
        <h1 style={{ fontSize, }}>  </h1>
      </Container>
    }
    return <Container width={this.props.width}>
        <div>
          <h1 style={{ color: '#0080FF', fontSize, }}> { config.title } </h1>
          <p style={{ fontSize, fontWeight: 'bold', color: 'orange', }}> QR </p>
          <p style={{ fontSize, fontWeight: 'bold', color: 'orange', }}>  </p>
          <p style={{ fontSize: fontSize*0.7, marginTop: 40, }}> QR </p>
          <p style={{ fontSize: fontSize*0.7, }}>  </p>
          <p style={{ fontSize: fontSize*0.7, marginTop: 20, }}>  </p>
        </div>
    </Container>
  }
}

const props = state => {
  return ({
    pathname: state.router.location.pathname,
    search: state.router.location.search,
    hash: state.router.location.hash,
    width: state.app.width,
    user: state.app.user,
  })
}

const actions = dispatch => ({
  resetVote: () => dispatch( reset() ),
  recept: () => dispatch( recept() ),
})

export default connect(props, actions)(App);



