import { combineReducers } from 'redux'
import app from './app'
import types from './types'
import 'whatwg-fetch'
import router from 'connected-react-router'
import io from 'socket.io-client'

export const socket = io();

const rootReducer = combineReducers({
  app,
})

const genURL = (url) => {
  return url;
  // return `http://localhost:3000${url}`;
}

const genVoteURL = (projectId) => {
  return genURL(`/vote/${projectId}`);
}

export default rootReducer

const loadInfo = async (projectId) => {
  let response = await fetch('/project', {
    method: 'POST',
    credentials: 'same-origin',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      projectId,
    })
  })
  return await response.json();
}

export const load = (projectId, callback) => async (dispatch, getState) => {
  const project = await loadInfo(projectId);
  dispatch({
    type: types.PARAMS,
    payload: {
      project,
    },
  })
  if (callback) callback();
}

export const makeQRCode = (projectId, callback) => async (dispatch, getState) => {
  const project = await loadInfo(projectId);
  const url = genVoteURL(project.id);;
  let response = await fetch('/qrcode', {
    method: 'POST',
    credentials: 'same-origin',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      string: url,
    })
  })
  const payload = await response.json();
  dispatch({
    type: types.PARAMS,
    payload: {
      qrcode: payload.qrcode,
      project,
    },
  })
}

export const loadQRCode = (url, key) => async (dispatch, getState) => {
  let response = await fetch('/qrcode', {
    method: 'POST',
    credentials: 'same-origin',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      string: genURL(url),
    })
  })
  const payload = await response.json();
  const qr = {}
  qr[key] = payload.qrcode;
  dispatch({
    type: types.PARAMS,
    payload: qr,
  })
}

export const push = (url) => (dispatch, getState) => {
  dispatch(router.push(url))
}

export const vote = (projectId, callback) => async (dispatch, getState) => {
  const { voted } = getState().app;
  const project = await loadInfo(projectId);
  console.log(project);
  if (project.id) {
    
    let response = await fetch('/vote', {
      method: 'POST',
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        projectId: project.id,
      })
    })
    const user = await response.json();

    voted[project.id] = project;
    dispatch({
      type: types.PARAMS,
      payload: {
        state: null,
        voted,
        project,
        user,
      },
    })
    if (callback) callback();
  } else {
    dispatch({
      type: types.PARAMS,
      payload: {
        state: 'not found',
      },
    })
  }
}

export const changeWindowSize = (size) => (dispatch, getState) => {
  dispatch({
    type: types.LAYOUT,
    payload: {
      ...size,
    },
  })
}

export const loadRanking = (size) => (dispatch, getState) => {
  socket.emit('reload', (ranking) => {

    if (ranking.length < 20) {
      const len = ranking.length;
      for (var i=0;i<20-len;i++) {
        ranking.push({
          title: [],
        });
      }
    }

    dispatch({
      type: types.PARAMS,
      payload: {
        statistics: ranking,
      },
    })
  });
}

