export const loadAllProject = (callback) => async (dispatch, getState) => {
  let response = await fetch('/projects', {
    method: 'POST',
    credentials: 'same-origin',
  })
  const projects = await response.json();

  for (var i=0;i<projects.length;i++) {
    const project = projects[i];
    const url = genVoteURL(project.id);
    let response = await fetch('/qrcode', {
      method: 'POST',
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        string: url,
      })
    })
    const payload = await response.json();
    project.qrCode = payload.qrcode;
  }

  if (callback) callback(projects);
}



