import React, { Component } from 'react';
import '../pages/App.css';
import { connect } from 'react-redux'

class QRHeader extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    const fontSize = ((w) => { let t = w / 15; if (t < 18) t = 18; return t; })(this.props.width);
    return (
      <div>
        <h2 className="QRMarkHeader" style={{
          margin: 0, fontSize: fontSize*0.7, position: 'absolute', top: 0,
          width: this.props.width,
        }}> {
          this.props.children
        } </h2>
      </div>
    )
  }
}

QRHeader.defaultProps = {
  width: 200,
}

export default QRHeader;



